import React, { Fragment, useState, useEffect } from 'react';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import AdvertisersTable from './AdvertisersTable';
import Pagination from '../../funcionalities/Pagination';
import '../../../styles/pagination.css';
import '../../../styles/administration.css';


function Advertisers({ credentials }) {

    console.log(credentials);

    const [advertisers, setAdvertisers] = useState([]);
    const [page, setPage] = useState(0);
    const [pages, setPages] = useState(0);

    //const alias = 'Paula';
    //const password = '123456789';

    useEffect(() => {
        fetch(`http://localhost:8090/advertisers?page=${page}`, {
            headers: {
                'Authorization': credentials.header
            }
        })
        .then((response) => response.json())
        .then((advertisersPage) => { 
             setAdvertisers(advertisersPage.content);
             setPages(advertisersPage.totalPages);
        });
    }, [setAdvertisers, page, credentials]);

    
    return (
        <Fragment>
            <AdvertisersTable advertisers={advertisers} setAdvertisers={setAdvertisers} credentials={credentials}/>
            <Link className="addItem" to={"/administrador/anunciantes/nuevo-anunciante"}><Button className="buttonAdd" variant="link" size="sm">Nuevo Anunciante</Button></Link>
            <Pagination page={page} pages={pages} setPage={setPage} />
        </Fragment>
    );
}

export default Advertisers;